<script>
	import Map from "./Map.svelte";
	import Main from "./Main.svelte";
	import Section from "./Section.svelte";
	import MapSource from "./MapSource.svelte";
	import MapLayer from "./MapLayer.svelte";
	import Select from "./Select.svelte";
	import LineChart from "./LineChart.svelte";
	import ColChart from "./ColChart.svelte";
	import PopChart from "./PopChart.svelte";

	import {
		getDataset,
		getPlace,
		getGeodata,
		makeOptions,
		makeLookup,
		inViewport,
		navTo,
		adjectify,
		plusminus,
		colors,
		getPyramid,
	} from "./utils.js";
	import { mapObj, mapSources } from "./stores.js";

	// Settings for loading datasets
	const baseurl = "https://bothness.github.io/ons-datasets/";
	const geodata = {
		url: "https://bothness.github.io/geo-data/topojson/pcon2018ew.json",
		layer: "pcon18ew",
	};
	const mapstyle =
		"https://bothness.github.io/ons-basemaps/data/style-omt.json";
	const datasets = [
		{
			code: "popchange",
			source: "csv/single/constituency-population-value-change-all.csv",
		},
		{
			code: "agemed",
			source: "csv/single/constituency-agemed-value-2011-all.csv",
		},
		{
			code: "density",
			source: "csv/single/constituency-density-value-2011-all.csv",
		},
	];
	const places = [
		{
			code: "E14000699",
			default: true,
		},
		{
			code: "K04000001",
		},
	];

	// Variables to hold data
	let data = {
		datasets: {},
		places: {},
		geojson: {},
		options: [],
		lookup: {},
		quantiles: [],
	};
	let selected = {
		place: null,
		dataset: null,
		section: null,
	};
	let sections = {};

	// Active locations on map
	let location = {
		selected: null,
		hovered: null,
	};

	// Loaded status for datasets
	let loaded = {
		datasets: false,
		places: false,
		geojson: false,
		options: false,
		lookup: false,
		quantiles: false,
	};
	$: allLoaded =
		loaded.datasets &&
		loaded.places &&
		loaded.geojson &&
		loaded.options &&
		loaded.lookup &&
		loaded.quantiles;

	// Load datasets
	for (let i in datasets) {
		getDataset(baseurl + datasets[i].source).then((result) => {
			data.datasets[datasets[i].code] = result;
			if (datasets[i].default) {
				selected.dataset = data.datasets[datasets[i].code];
			}
			if (i == datasets.length - 1) {
				loaded.datasets = true;
				
				// Load topojson data to geojson object
				getGeodata(geodata.url, geodata.layer)
					.then((result) => {
						data.geojson = result;
						loaded.geojson = true;
						return result.features;
					})
					.then((features) => {
						// Generate list of place codes & names for selector
						let options = makeOptions(features);
						let codes = data.datasets[datasets[0].code].data.map(d => d.geocode);

						data.options = options.filter(d => codes.includes(d.geocode));
						loaded.options = true;

						// Generate lookup for place names
						data.lookup = makeLookup(data.options);
						loaded.lookup = true;

						// Set rank quantiles based on count of features
						let q10 = data.options.length * 0.1;
						for (let i = 1; i < 10; i++) {
							data.quantiles.push(Math.floor(q10 * i));
						}
						data.quantiles.push(data.options.length);
						loaded.quantiles = true;
					});

				// Load places
				for (let i in places) {
					getPlace(baseurl + "json/place/" + places[i].code + ".json").then(
						(result) => {
							data.places[places[i].code] = result;
							if (places[i].default) {
								selected.place = places[i].code;
							}
							if (i == places.length - 1) {
								loaded.places = true;
							}
						}
					);
				}
			}
		});
	}

	// Listener for scroll events
	window.addEventListener("scroll", function (e) {
		if (sections[0]) {
			for (var i in sections) {
				if (sections[i].el && inViewport(sections[i].el)) {
					if (selected.section != sections[i]) {
						selected.section = sections[i];
						if (selected.dataset != data.datasets[selected.section.dataset]) {
							selected.dataset = data.datasets[selected.section.dataset];
						}
						navTo(selected.section.location, $mapObj, data.geojson);
						location.selected = selected.section.location.geocode
							? selected.section.location.geocode
							: null;
					}
					break;
				}
			}
		}
	});

	// Get place data when option is selected
	$: if (loaded.places && selected.place && !data.places[selected.place]) {
		getPlace(baseurl + "json/place/" + selected.place + ".json").then(
			(result) => {
				data.places[selected.place] = result;
			}
		);
	}
</script>

<style>
	:global(body) {
		margin: 0;
		padding: 0;
	}
	:global(h1) {
		font-size: 2.8em;
	}
	small {
		color: #777;
	}
</style>

{#if allLoaded}
	<Main>
		<Section type="header" bind:data={sections[0]} dataset={null}>
			<span slot="title">How our population is changing</span>
			<span slot="description">
				<p>This is a dynamic scrollytelling demo with semi-automated data-driven content.</p>
				<p>It highlights demographic changes in parliamentary constituencies in England and Wales between 2001 and 2011.</p>
				<p><small>(Please note that this interactive covers only constituencies in England and Wales.)</small></p>
			</span>
		</Section>
		<Section bind:data={sections[1]} dataset="popchange">
			<span slot="title">There are more people</span>
			<span slot="description">
				<p>
					The population of England and Wales in 2011 was
					{data.places['K04000001'].data.age10yr.value['2011'].all.toLocaleString()},
					an increase of
					{data.places['K04000001'].data.age10yr.value.change.all}% in the
					decade from 2001.
				</p>
				<LineChart
					data={[{ x: 1981, y: data.places['K04000001'].data.age10yr.value['1981'].all }, { x: 1991, y: data.places['K04000001'].data.age10yr.value['1991'].all }, { x: 2001, y: data.places['K04000001'].data.age10yr.value['2001'].all }, { x: 2011, y: data.places['K04000001'].data.age10yr.value['2011'].all }]} />
			</span>
		</Section>
		<Section
			bind:data={sections[2]}
			dataset="popchange"
			location={{ geocode: data.datasets.popchange.top5[0].geocode }}>
			<span slot="title">Fastest growing</span>
			<span slot="description">
				<p>
					The most rapid population growth was seen in the constituencies of
					<strong>{data.lookup[data.datasets.popchange.top5[0].geocode]}</strong>
					(+{data.datasets.popchange.top5[0].value}%),
					<strong>{data.lookup[data.datasets.popchange.top5[1].geocode]}</strong>
					(+{data.datasets.popchange.top5[1].value}%) and
					<strong>{data.lookup[data.datasets.popchange.top5[2].geocode]}</strong>
					(+{data.datasets.popchange.top5[2].value}%).
				</p>
				<ColChart
					data={data.datasets.popchange.data}
					yMax={data.datasets.popchange.top5[0].value}
					yMin={data.datasets.popchange.bottom5[0].value}
					selected={{ geocode: data.datasets.popchange.top5[0].geocode, name: data.lookup[data.datasets.popchange.top5[0].geocode] }} />
			</span>
		</Section>
		<Section
			bind:data={sections[3]}
			dataset="popchange"
			location={{ geocode: data.datasets.popchange.bottom5[0].geocode }}>
			<span slot="title">Declining population</span>
			<span slot="description">
				<p>
					The constituencies with the fastest shrinking population were
					<strong>{data.lookup[data.datasets.popchange.bottom5[0].geocode]}</strong>
					({data.datasets.popchange.bottom5[0].value}%),
					<strong>{data.lookup[data.datasets.popchange.bottom5[1].geocode]}</strong>
					({data.datasets.popchange.bottom5[1].value}%) and
					<strong>{data.lookup[data.datasets.popchange.bottom5[2].geocode]}</strong>
					({data.datasets.popchange.bottom5[2].value}%).
				</p>
				<ColChart
					data={data.datasets.popchange.data}
					yMax={data.datasets.popchange.top5[0].value}
					yMin={data.datasets.popchange.bottom5[0].value}
					selected={{ geocode: data.datasets.popchange.bottom5[0].geocode, name: data.lookup[data.datasets.popchange.bottom5[0].geocode] }} />
			</span>
		</Section>
		<Section bind:data={sections[4]} dataset="density">
			<span slot="title">Growing density</span>
			<span slot="description">
				<p>
					England and Wales is one of the most densely populated regions in the
					world. Between 2001 and 2011 the population rose from
					{data.places['K04000001'].data.density.value['2001'].all}
					to
					{data.places['K04000001'].data.density.value['2011'].all}
					people per hectare.
				</p>
				<LineChart
					data={[{ x: 1981, y: data.places['K04000001'].data.density.value['1981'].all }, { x: 1991, y: data.places['K04000001'].data.density.value['1991'].all }, { x: 2001, y: data.places['K04000001'].data.density.value['2001'].all }, { x: 2011, y: data.places['K04000001'].data.density.value['2011'].all }]} />
			</span>
		</Section>
		<Section
			bind:data={sections[5]}
			dataset="density"
			location={{ geocode: data.datasets.density.top5[0].geocode }}>
			<span slot="title">Most densely populated</span>
			<span slot="description">
				<p>
					The constituencies with the highest population density were
					<strong>{data.lookup[data.datasets.density.top5[0].geocode]}</strong>
					({data.datasets.density.top5[0].value}
					persons per hectare),
					<strong>{data.lookup[data.datasets.density.top5[1].geocode]}</strong>
					({data.datasets.density.top5[1].value}
					persons per hectare) and
					<strong>{data.lookup[data.datasets.density.top5[2].geocode]}</strong>
					({data.datasets.density.top5[2].value}
					persons per hectare).
				</p>
				<ColChart
					data={data.datasets.density.data}
					yMax={data.datasets.density.top5[0].value}
					yMin={data.datasets.density.bottom5[0].value}
					selected={{ geocode: data.datasets.density.top5[0].geocode, name: data.lookup[data.datasets.density.top5[0].geocode] }} />
			</span>
		</Section>
		<Section
			bind:data={sections[6]}
			dataset="density"
			location={{ geocode: data.datasets.density.bottom5[0].geocode }}>
			<span slot="title">Most sparsely populated</span>
			<span slot="description">
				<p>
					The constituencies with the lowest population density were
					<strong>{data.lookup[data.datasets.density.bottom5[0].geocode]}</strong>
					({data.datasets.density.bottom5[0].value}
					persons per hectare),
					<strong>{data.lookup[data.datasets.density.bottom5[1].geocode]}</strong>
					({data.datasets.density.bottom5[1].value}
					persons per hectare) and
					<strong>{data.lookup[data.datasets.density.bottom5[2].geocode]}</strong>
					({data.datasets.density.bottom5[2].value}
					persons per hectare).
				</p>
				<ColChart
					data={data.datasets.density.data}
					yMax={data.datasets.density.top5[0].value}
					yMin={data.datasets.density.bottom5[0].value}
					selected={{ geocode: data.datasets.density.bottom5[0].geocode, name: data.lookup[data.datasets.density.bottom5[0].geocode] }} />
			</span>
		</Section>
		<Section bind:data={sections[7]} dataset="agemed">
			<span slot="title">People are getting older</span>
			<span slot="description">
				<p>
					The median age in England and Wales is on the rise. Whereas the median
					age in 2001 was
					{data.places['K04000001'].data.agemed.value['2001'].all}
					years, by 2011 it had risen to
					{data.places['K04000001'].data.agemed.value['2011'].all}.
				</p>
				<PopChart
					data={getPyramid(data.places['K04000001'].data.age10yr.value)} />
			</span>
		</Section>
		<Section
			bind:data={sections[8]}
			dataset="agemed"
			location={{ geocode: data.datasets.agemed.top5[0].geocode }}>
			<span slot="title">Oldest population</span>
			<span slot="description">
				<p>
					The constituencies with the highest median age were
					<strong>{data.lookup[data.datasets.agemed.top5[0].geocode]}</strong>
					({data.datasets.agemed.top5[0].value}
					years),
					<strong>{data.lookup[data.datasets.agemed.top5[1].geocode]}</strong>
					({data.datasets.agemed.top5[1].value}
					years) and
					<strong>{data.lookup[data.datasets.agemed.top5[2].geocode]}</strong>
					({data.datasets.agemed.top5[2].value}
					years).
				</p>
				<ColChart
					data={data.datasets.agemed.data}
					yMax={data.datasets.agemed.top5[0].value}
					yMin={data.datasets.agemed.bottom5[0].value}
					selected={{ geocode: data.datasets.agemed.top5[0].geocode, name: data.lookup[data.datasets.agemed.top5[0].geocode] }} />
			</span>
		</Section>
		<Section
			bind:data={sections[9]}
			dataset="agemed"
			location={{ geocode: data.datasets.agemed.bottom5[0].geocode }}>
			<span slot="title">Youngest population</span>
			<span slot="description">
				<p>
					The constituencies with the lowest median age were
					<strong>{data.lookup[data.datasets.agemed.bottom5[0].geocode]}</strong>
					({data.datasets.agemed.bottom5[0].value}
					years),
					<strong>{data.lookup[data.datasets.agemed.bottom5[1].geocode]}</strong>
					({data.datasets.agemed.bottom5[1].value}
					years) and
					<strong>{data.lookup[data.datasets.agemed.bottom5[2].geocode]}</strong>
					({data.datasets.agemed.bottom5[2].value}
					years).
				</p>
				<ColChart
					data={data.datasets.agemed.data}
					yMax={data.datasets.agemed.top5[0].value}
					yMin={data.datasets.agemed.bottom5[0].value}
					selected={{ geocode: data.datasets.agemed.bottom5[0].geocode, name: data.lookup[data.datasets.agemed.bottom5[0].geocode] }} />
			</span>
		</Section>
		<Section bind:data={sections[10]} dataset="agemed">
			<span slot="title">Find your constituency</span>
			<span slot="description">
				<p>Select a constituency to see how it compares to the country as a whole.</p>
				<Select
					options={data.options}
					bind:selected={selected.place}
					message="Select a local constituency" />
			</span>
		</Section>
		{#if data.places[selected.place]}
			<Section
				bind:data={sections[11]}
				dataset="popchange"
				location={{ geocode: selected.place }}>
				<span slot="title">
					{#if data.places[selected.place].data.population.value['change'].all > 0}
						Growing population
					{:else}Shrinking population{/if}
				</span>
				<span slot="description">
					<p>
						<strong>{data.places[selected.place].name}</strong>
						had a population of
						{data.places[selected.place].data.population.value['2011'].all.toLocaleString()}
						on Census day 2011,
						{plusminus(data.places[selected.place].data.population.value['change'].all, '%')},
						which was
						{adjectify(
							data.quantiles,
							data.places[selected.place].data.population.value_rank['change']
								.all,
							['lower', 'higher']
						)}
						the average
						{data.places['K04000001'].data.population.value['change'].all}%
						increase across all local constituencies.
					</p>
					<LineChart
						data={[{ x: 2001, y: data.places[selected.place].data.age10yr.value['2001'].all }, { x: 2011, y: data.places[selected.place].data.age10yr.value['2011'].all }]} />
				</span>
			</Section>
			<Section
				bind:data={sections[12]}
				dataset="density"
				location={{ geocode: selected.place }}>
				<span slot="title">
					{#if data.places[selected.place].data.density.value_rank['2011'].all < data.quantiles[3]}
						Lower density
					{:else if data.places[selected.place].data.density.value_rank['2011'].all < data.quantiles[5]}
						Density
					{:else}Higher density{/if}
				</span>
				<span slot="description">
					<p>
						<strong>{data.places[selected.place].name}'s</strong>
						population density was
						{data.places[selected.place].data.density.value['2011'].all}
						people per hectare,
						{adjectify(
							data.quantiles,
							data.places[selected.place].data.density.value_rank['2011'].all,
							['lower', 'higher']
						)}
						average of
						{data.places['K04000001'].data.density.value['2011'].all}
						across England and Wales.
					</p>
					<ColChart
						data={data.datasets.density.data}
						yMax={data.datasets.density.top5[0].value}
						yMin={data.datasets.density.bottom5[0].value}
						selected={{ geocode: selected.place, name: data.lookup[selected.place] }} />
				</span>
			</Section>
			<Section
				bind:data={sections[13]}
				dataset="agemed"
				location={{ geocode: selected.place }}>
				<span slot="title">
					{#if data.places[selected.place].data.agemed.value_rank['2011'].all < data.quantiles[3]}
						Younger population
					{:else if data.places[selected.place].data.agemed.value_rank['2011'].all < data.quantiles[5]}
						Age
					{:else}Older population{/if}
				</span>
				<span slot="description">
					<p>
						The median age of
						<strong>{data.places[selected.place].name}</strong>
						is
						{adjectify(
							data.quantiles,
							data.places[selected.place].data.agemed.value_rank['2011'].all,
							['younger', 'older']
						)}
						the average across constituencies, with a median age of
						{data.places[selected.place].data.agemed.value['2011'].all}
						years ({data.places['K04000001'].data.agemed.value['2011'].all}
						years average),
						{plusminus(data.places[selected.place].data.agemed.value['2011'].all - data.places[selected.place].data.agemed.value['2001'].all, ' years', ' year')}.
					</p>
					<PopChart
						data={getPyramid(data.places[selected.place].data.age10yr.value)} />
				</span>
			</Section>
		{/if}
	</Main>

	<Map style={mapstyle}>
		<MapSource
			id="pcon"
			type="geojson"
			data={data.geojson}
			promoteId="pcon18cd">
			<MapLayer id="fill" source="pcon" type="fill" data={selected.dataset} />
			<MapLayer
				id="boundaries"
				source="pcon"
				type="line"
				selected={location.selected}
				paint={{ 'line-color': 'rgb(0, 0, 0)', 'line-width': ['case', ['==', ['feature-state', 'selected'], true], 3, 0.5], 'line-opacity': ['case', ['==', ['feature-state', 'selected'], true], 1, 0.3] }} />
		</MapSource>
	</Map>
{/if}
